.TopHeader {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    height: 40px;
    border: 1px solid #eee;
    box-shadow: 0 8px 6px -10px black;
}

.TopHeader-logo {
    width: 120px;
    height: 30px;
    margin-right: 10px;
}

.TopHeader-logo img {
    width: 100%;
    height: 100%;
}

.TopHeader-nav a{
    border-left: 1px solid black;
    padding: 0px 10px;
    text-decoration: none;
    color: black
}